.AddListItem {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 20px;
    background-color: lightgray;
    width: 300px;
    border: 1px solid black;
    padding-bottom: 18.7px;
  }
  
  .AddListItem_form {
    background-color: white;
    padding: 0px 5px;
    border: 1px solid black;
    width: 270px;
    margin-top: 10px;
    display: flex;
    flex-direction: column;
    color: black;
    text-decoration: none;
  }
  
  .AddListItem_bottom {
    display: flex;
    justify-content: center;
    margin: 10px 0px;
  }
  
  .AddListItem_button {
    margin: 5px;
  }
  
  .AddListItem_label {
    margin: 5px 0px;
  }
  
  #form_notes {
    height: 75px;
  }
  
  